/* emoji toggle */
.emoji_toggle 
{
    position: absolute;
    display: block;

    bottom: 0px;
    right: 15px;
    /* border: 1px   solid red; */

    color: rgb(126, 126, 126);
    font-size: 1.7em;

    transition: .1s;
    cursor: pointer;
}
.emoji_toggle:active
{
    transform: scale(0.95);
}
.emoji_toggle:hover 
{
    color: rgb(179, 179, 179);
}
/* emoji scrollbar */
