{
	width: 10px;
	background-color: #413f42;
}

.emoji-mart-scroll::-webkit-scrollbar-thumb
{
	background-color: #AAA;
	border-radius: 10px;
	background-image: -webkit-linear-gradient(90deg,
	 rgba(0, 0, 0, .4) 25%,
	 transparent 25%,
	 transparent 50%,
	 rgba(0, 0, 0, .2) 50%,
	 rgba(0, 0, 0, .4) 75%,
	 transparent 75%,
	 transparent)
}
 
