/*! feelings items */
/* wrapper */
.feelings_items_wrapper
{
    /* border: 1px solid red; */
    border-top: 1px solid #535353;
    overflow-y: scroll;

    width: 500px;
    height: 220px;

    padding: 7px 16px 0 16px;
    margin-top: .7em;

    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 48px 48px;
}
/* items */
.feeling_item
{
    list-style: none;
    /* border: 1px solid green; */

    background: rgb(0, 0, 0, 0);
    color: rgb(148, 148, 148);
    font-size: 15px;

    display: flex;
    justify-content: flex-start;
    align-items: center;

    height: 48px;
    padding: 0 8px;
    border-radius: 8px;
    cursor: pointer;
    transition: .2s;
}
.feeling_item:hover
{
    background: rgb(0, 0, 0, .3);
    color: rgb(196, 196, 196);
}
/* icon wrapper */
.feeling_icon
{
    /* border: 1px solid red; */

    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    padding: 6px;
    margin-right: 10px;
    border-radius: 50%;

    background: rgb(54, 54, 54);
    box-shadow: 0px 0px 4px rgb(0, 0, 0, 0.3);
    transition: all .2s;
}
.feeling_icon::after
{
    content: '';
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background: rgb(0, 0, 0, 0.3);
    border-radius: 50%;

    transition: all .1s;
}
.feeling_item:hover .feeling_icon
{
    background: rgb(82, 82, 82);
}
.feeling_item:hover .feeling_icon::after
{
    background: rgb(0, 0, 0, 0);
}
/* icon */
.feeling_icon img
{
    width: 20px;
    height: 20px;
    position: relative;

    box-shadow: 0px 0px 4px rgb(0, 0, 0, 0.3);
    border-radius: 50%;
}
/*! feelings items scrollbar */
.feelings_items_wrapper::-webkit-scrollbar-track
{
	/* -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #413f42; */
	border-radius: 10px;
}

.feelings_items_wrapper::-webkit-scrollbar
{
	width: 8px;
	background-color: #413f42;
}

.feelings_items_wrapper::-webkit-scrollbar-thumb
{
	background-color: rgb(49, 49, 49);
	border-radius: 10px;
}

/*! Feeling Status */
